import React from 'react';
import {MDBBtn} from "mdbreact";
import {Link} from "react-router-dom";
import Header from "./Header";

const Type = () => {
    return (
        <>
            <Header text={"Wat wilt u melden?"} backLink={"/"}/>
            <Link to={"/incident"}>
                <MDBBtn color="light-blue" block>Incident</MDBBtn>
            </Link>
            <p>bv: Val, prik, etc.</p>
            <Link to={"/incident"}>
                <MDBBtn color="light-blue" block>Bijna-incident</MDBBtn>
            </Link>
            <p>bv: Vechten met de popo's</p>
        </>
    )
};

export default Type;
