import React from 'react';
import Header from "./Header";
import YesNoQuestion from "./questions/YesNoQuestion";
import OpenQuestion from "./questions/OpenQuestion";
import DateTimeQuestion from "./questions/DateTimeQuestion";
import {MDBBtn, MDBIcon} from "mdbreact";

const questions = [
    <YesNoQuestion question={"Was er een getuige aanwezig?"}/>,
    <OpenQuestion question={"Wat was de locatie?"} label={"Locatie"}/>,
    <DateTimeQuestion question={"Wanneer gebeurde het incident?"}/>,
    <DateTimeQuestion
        question={"Wanneer werd de leidinggevende of werkgever op de hoogte gebracht van het incident?"}/>,
    <OpenQuestion question={"Geef een korte omschrijving van de gebeurtenis"}/>,
]

class Questions extends React.Component {
    state = {
        index: 0
    }

    previous = () => this.setState({index: this.state.index - 1})
    next = () => this.setState({index: this.state.index + 1})

    previousDisabled = () => this.state.index <= 0;
    nextDisabled = () => this.state.index >= questions.length - 1;

    render() {
        return (
            <>
                <Header text={"Vragen"} backLink={"/Incident"}/>
                {questions[this.state.index]}
                <div className="arrows">
                    <MDBBtn disabled={this.previousDisabled()} onClick={this.previous}>
                        <MDBIcon icon={"fa fa-chevron-left"}/>
                    </MDBBtn>
                    <p className="mb-0">{this.state.index + 1} / {questions.length}</p>
                    <MDBBtn disabled={this.nextDisabled()} onClick={this.next}>
                        <MDBIcon icon={"fa fa-chevron-right"}/>
                    </MDBBtn>
                </div>
            </>
        )
    }
}

export default Questions;
