import React from 'react';
import {MDBCol, MDBRow} from "mdbreact";
import Header from "./Header";
import IncidentItem from "./IncidentItem";

const incidents = [
    {title: 'Agressie', to: '/questions', img: '/incident/fist.png', color: '#2BBBAD'},
    {title: 'Arbeidsongeval', to: '/questions', img: '/incident/helmet.png', color: '#4285F4'},
    {title: 'Brand', to: '/questions', img: '/incident/flame.png', color: '#aa66cc'},
    {title: 'Bijna ongeval', to: '/questions', img: '/incident/accident.png', color: '#ff4444'},
    {title: 'EHBO', to: '/questions', img: '/incident/ehbo.png', color: '#ffbb33'},
    {title: 'Nood', to: '/questions', img: '/incident/sos.png', color: '#00C851'},
    {title: 'Prik-, Snij-, Spatongeval', to: '/questions', img: '/incident/blood.png', color: '#33b5e5'},
    {title: 'Ziekenhuis noodplanning', to: '/questions', img: '/incident/hospital.png', color: '#00695c'},
];

const Incident = () => {
    return (
        <>
            <Header text={"Welk type incident?"} backLink={"/type"}/>
            <MDBRow>
                {incidents.map((incident, i) => {
                    return (
                        <MDBCol key={i} size={4} className="incident-col">
                            <IncidentItem {...incident} />
                        </MDBCol>
                    )
                })}
            </MDBRow>
        </>
    )
};

export default Incident;
