import React from 'react';

const YesNoQuestion = ({question}) => {
    return (
        <>
            <p>{question}</p>
            <form>

                <div className="custom-control custom-radio">
                    <input type="radio" id="r1" name="radio" className="custom-control-input"/>
                    <label className="custom-control-label" htmlFor="r1">Ja</label>
                </div>
                <div className="custom-control custom-radio">
                    <input type="radio" id="r2" name="radio" className="custom-control-input"/>
                    <label className="custom-control-label" htmlFor="r2">Nee</label>
                </div>
            </form>
        </>
    )
};

export default YesNoQuestion;
